.planets_container {
  display: flex;
  flex-direction: column;
  cursor: pointer;
  user-select: none;

  &-upper {
    display: flex;
    width: 200px;
    justify-content: flex-start;
    border: 1px solid slateblue;
    margin-bottom: 5px;
    margin-top: 20px;
    padding: 3px 10px;
  }

  &-lower {
    white-space: pre-line;
    max-height: 10000px;
  }
}


.fadeHeight-enter-active {
  transition: all 0.5s ease;
}
.fadeHeight-enter-from {
  opacity: 0;
  max-height: 0;
}
.fadeHeight-enter-to {
  opacity: 1;
  max-height: 10000px;
}

.fadeHeight-leave-active {
  max-height: 0;
  transition: all 1s;
}

</style>
